import { GlobalDataService } from './../../../shared/services/globalData/global-data.service';
import { Router } from '@angular/router';
import { Component, OnInit } from '@angular/core';
import { RouterConstants } from 'src/app/shared/config/constants/router.constants';

@Component({
  selector: 'app-about',
  templateUrl: './about.page.html',
  styleUrls: ['./about.page.scss'],
})
export class AboutPage implements OnInit {

  RouterConstants = RouterConstants;
  constructor(
    private router: Router,
    private globalData: GlobalDataService,
  ) { }

  ngOnInit() {
  }

  async openLegal() {
    this.router.navigateByUrl(RouterConstants.LEGAL_ADVICE);
  }

  openMarket() {
    this.globalData.openMarket();
  }

}
